import TDNode, { TDNodeType } from "./TDNode";
import StringBuilder from "./StringBuilder";
export default class TDJSONParser {
    static get() { return TDJSONParser.instance; }
    parse(opt) { return this.parseFromSource(opt.source, opt, new TDNode()); }
    parseFromSource(src, opt, node) {
        if (!TDJSONParser.skipSpaceAndComments(src))
            return node;
        const c = src.peek();
        node.start = src.getPos();
        try {
            if (c === '{')
                return this.parseMap(src, opt, node, true);
            if (c === '[')
                return this.parseArray(src, opt, node, true);
            if (node.isRoot()) {
                switch (opt.defaultRootType) {
                    case TDNodeType.MAP:
                        return this.parseMap(src, opt, node, false);
                    case TDNodeType.ARRAY:
                        return this.parseArray(src, opt, node, false);
                }
            }
            if (c === '"' || c === '\'' || c === '`') {
                src.read();
                const sb = new StringBuilder();
                src.readQuotedToString(c, sb);
                this.readContinuousString(src, sb);
                return node.setValue(sb.toString());
            }
            const str = src.readUntilTermintor(",}]\n\r\t", 1, Number.MAX_VALUE).trim();
            if ("null" === str)
                return node.setValue(null);
            if ("true" === str)
                return node.setValue(true);
            if ("false" === str)
                return node.setValue(false);
            if (str.startsWith("0x") || str.startsWith(("0X")))
                return node.setValue(this.parseNumber(str.substring(2), true));
            if (c === '-' || c === '+' || c === '.' || (c >= '0' && c <= '9'))
                return node.setValue(this.parseNumber(str, false));
            return node.setValue(str);
        }
        finally {
            node.length = src.getPos() - node.start;
        }
    }
    readContinuousString(src, sb) {
        while (TDJSONParser.skipSpaceAndComments(src)) {
            const c = src.peek();
            if ("\"`'".indexOf(c) < 0)
                break;
            src.read();
            src.readQuotedToString(c, sb);
        }
    }
    /**
     * @return true if there's more text left
     */
    static skipSpaceAndComments(src) {
        while (src.skipSpaces()) {
            const c = src.peek();
            if (c === '#') {
                if (src.skipUntilTerminator("\n"))
                    src.skip(1);
                continue;
            }
            if (c !== '/' || src.isEof(1))
                return true;
            const c1 = src.peek(1);
            switch (c1) {
                case '/': // line comments
                    if (src.skipUntilTerminator("\n"))
                        src.skip(1);
                    break;
                case '*': // block comments
                    src.skip(2);
                    src.skipUntilMatch("*/", true);
                    break;
                default:
                    return true;
            }
        }
        return false;
    }
    parseMap(src, opt, node, withStartBracket) {
        node.type = TDNodeType.MAP;
        if (withStartBracket)
            src.read();
        while (true) {
            if (!TDJSONParser.skipSpaceAndComments(src)) {
                if (withStartBracket)
                    throw src.createParseRuntimeException("EOF encountered while expecting matching '}'");
                break;
            }
            let c = src.peek();
            if (c === '}') {
                src.read();
                break;
            }
            if (c === ',') { // Skip ,
                src.read();
                continue;
            }
            let key;
            if (c === '"' || c === '\'' || c === '`') {
                src.read();
                key = src.readQuotedString(c);
                if (!TDJSONParser.skipSpaceAndComments(src))
                    break;
                c = src.peek();
                if (c !== ':' && c !== '{' && c !== '[')
                    throw src.createParseRuntimeException("No ':' after key:" + key);
            }
            else {
                key = src.readUntilTermintor(":{[", 1, Number.MAX_VALUE).trim();
                if (src.isEof())
                    throw src.createParseRuntimeException("No ':' after key:" + key);
                c = src.peek();
            }
            if (c === ':')
                src.read();
            this.parseFromSource(src, opt, node.createChild(key));
        }
        return node;
    }
    parseArray(src, opt, node, withStartBracket) {
        node.type = TDNodeType.ARRAY;
        if (withStartBracket)
            src.read();
        while (true) {
            if (!TDJSONParser.skipSpaceAndComments(src)) {
                if (withStartBracket)
                    throw src.createParseRuntimeException("EOF encountered while expecting matching ']'");
                break;
            }
            const c = src.peek();
            if (c === ']') {
                src.read();
                break;
            }
            if (c === ',') {
                src.read();
                continue;
            }
            this.parseFromSource(src, opt, node.createChild());
        }
        return node;
    }
    parseNumber(str, isHex) {
        const num = (!isHex && str.indexOf('.') >= 0) ?
            parseFloat(str) :
            parseInt(str, isHex ? 16 : 10);
        return Number.isNaN(num) ? str : num;
    }
}
TDJSONParser.instance = new TDJSONParser();
